                  previous: true,
                },
                input: false,
                smev_req: {},
                theme: "info",
                key: "panel6",
                esiaKey: {},
              },
            ],
            legend: "   ",
            dataGridLabel: false,
            tableView: false,
            require: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}],
            label: "   ",
            type: "fieldset",
            input: false,
            smev_req: {},
            key: "fieldSet2",
            esiaKey: {},
          },
          {
            extTypeCheckbox: false,
            epgu_selected_vs: {},
            dataGridLabel: false,
            tableView: false,
            require: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}],
            label:
              ",    ",
            type: "file",
            url: "/o/template/files/uploadFormFile",
            fileTypes: [
              {
                label: "",
                value: "",
              },
            ],
            input: true,
            webcam: false,
            smev_req: {},
            key: "file1",
          },
        ],
      },
    };
  },

  components: {
    Loader,
    MeasureApplicationForm,
  },

  methods: {
    onLoad() {
      this.loading = true;
      setTimeout(() => {
        this.loading = false;
      }, 5000);
    },
    ajaxRequest(service, id, responseTarget, log) {
      const xhr = new XMLHttpRequest();
      const url =
        // "https://open-newtemplate.isands.ru/open-core/api/serv/" +
        // "http://192.168.18.171:8080/open-core/api/serv/" +
        // "http://192.168.18.171:8080/api/serv/" +
        "https://open-demo.isands.ru/api/serv/" +
        service +
        "?id=" +
        id;
      xhr.open("GET", url);
      xhr.responseType = "json";
      xhr.onload = () => {
        console.log(log);
        console.log(xhr.response);
        this[responseTarget] = xhr.response;
      };
      xhr.send();
    },
    getMeasure() {
      this.ajaxRequest(
        "get-model",
        this.$route.params.subId,
        "measure",
        "   "
      );
    },
    getForms() {
      this.ajaxRequest(
        "get-forms",
        this.$route.params.subId,
        "measureForms",
        " "
      );
    },
    getStartForm() {
      this.ajaxRequest(
        "get-appData",
        this.$route.params.subId,
        "startForm",
        " "
      );
    },
    measurePeriod() {
      let period = "";
      const options = {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
      };
      if (this.measure.startDate) {
        period +=
          "c " +
          new Intl.DateTimeFormat("ru-RU", options).format(
            new Date(this.measure.startDate)
          ) +
          " ";
      }
      if (this.measure.endDate) {
        period +=
          " " +
          new Intl.DateTimeFormat("ru-RU", options).format(
            new Date(this.measure.endDate)
          );
      }
      if (!this.measure.startDate && !this.measure.endDate) {
        period = "  ";
      }
      return period;
    },
  },

  mounted: function () {
    this.getMeasure();
    this.getForms();
    this.getStartForm();
    console.log(this.asd.applicationDTO.form.scheme);
  },
};
</script>

<style lang="scss">
$gap: 1rem;
$basic-color: var(--primary);
$text-color: #212529;
$support-color: grey;
$passive-color: #e4e4e4;
$active-color: #fff;

.page-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  .content {
    flex: 1;
  }
}

.support {
  padding-top: $gap/2;

  .support__header {
    .support__name {
      margin-bottom: $gap;
    }

    .support__details {
      & > * {
        margin-bottom: $gap;
      }

      @media (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      .support__date {
        color: $support-color;

        b {
          color: $text-color;
        }
      }
    }
  }

  hr {
    margin-top: 0;
  }

  .support__card {
    .support__card__header {
      display: flex;
      justify-content: space-between;

      & > * {
        margin-bottom: $gap;
      }

      .support__owner {
        .support__owner__header {
          color: $support-color;
        }

        .support__owner__logo {
          display: flex;
          font-weight: bolder;
          align-items: center;

          .org-logo {
            height: 38px;
            margin-right: $gap/2;
            margin-top: $gap/2;
          }

          .support__owner__name {
            margin-top: $gap/2;
          }
        }
      }

      .support__conditions {
        @media (min-width: 768px) {
          text-align: right;
        }

        .support__conditions__header {
          color: $support-color;
        }

        .support__size {
          font-size: 1.75rem;
          font-weight: 500;
        }

        .support__size__info {
          color: $support-color;
        }
      }

      .support__buttons {
        display: flex;
        align-items: start;

        @media (min-width: 768px) {
          justify-content: flex-end;
        }

        .support__icon {
          height: 38px;
          border: 1px solid $basic-color;
          border-radius: 0.25rem;
          margin-right: $gap/2;
        }
      }
    }

    hr {
      margin-top: 0;
    }

    .card {
      border: none;

      .nav {
        background-color: transparent;
        padding: 0;
        margin-bottom: $gap;

        @media (min-width: 768px) {
          padding-right: 1.25rem;
        }
      }

      .tab-content {
        margin-bottom: $gap;

        .tab-pane {
          padding: 0;

          @media (min-width: 768px) {
            padding-left: 1.25rem;
          }
        }

        ul {
          padding-left: 1.25rem;
        }

        :nth-last-child(1) {
          margin-bottom: 0;
        }
      }
    }
  }
}
</style>
